export default function Construction() {
    return (
        <article className="page">
            <Info />
            <Link title="return home" url="/"/>
        </article>
    );
}

const Link = ({ title, url}) => {
    return (
        <a className="link" href={url}>
            <div className="link-title">
                <h3 class="unselectable">{title}</h3>
            </div>
        </a>
    );
};

const Info = () => {
    return (
        <section>
            <h2 class="unselectable">under construction</h2>
            <p>
                This page is currently under construction, meaning that it hasn't been created yet!
            </p><br/>
            <p>
                Cy4 is probably being super lazy instead of working on it, meaning you're gonna have to wait a day or two to view the content here. Oops!
            </p>
        </section>
    );
};